<template>
	<view>
		<view>
			<search></search>
		</view>
		<view>
			<u-swiper :list="banner" :effect3d="true" height=330 effect3d-previous-margin=60 bg-color="#ffffff"></u-swiper>
		</view>
	</view>
</template>

<script>
	import {
		shoprequest
	} from "../../../servers/natework.js"
		import search from "../../../components/search/search"
	export default {
		data() {
			return {
				banner: [],
				recommed: []
			};
		},
		components:{
			search
		},
		onLoad() {
			this.getHomewrapper()
			this.getData()
		},
		onShow() {

		},
		methods: {
			getHomewrapper() {
				shoprequest({
					url: "/home/multidata"
				}, res => {
					this.banner = res.data.data.banner.list;
					this.recommed = res.data.data.recommend.list;
					console.log(this.banner)
				})
			},
			async getData() {
				let floor = await this.$usershop({
					url: "/home/swiperdata"
				})
				console.log(floor)
			}
		}
	}
</script>

